import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {fetchDetail, lockSlot, toggleELabelState, toggleEnableState, updateSlot, zeroOne} from '../../../api/slot';
import {ActionSheet, Button, InputItem, List, Modal, Switch, Toast, WingBlank} from 'antd-mobile';
import {setTitle} from "../../../store/actionCreators";
import {connect} from 'react-redux';
import FloatButton from '../../commons/FloatButton';
import './SlotDetailSetting.sass';

const ActionButtons = [
    '',
    '',
    '/',
    "",
    '',
];
const {Item} = List;
const mapAction2Props = (dispatch, props) => {
    return {
        ...props,
        setTitle: (...args) => dispatch(setTitle(...args)),
    }
};
const CAN_UPDATE_SLOT_INFO = false;


class SlotDetailSetting extends Component {
    constructor(props) {
        super(props);
        this.state = {
            slot: {}
        };
        this.slotId = this.props.match.params.id;
    }

    componentDidMount() {
        this.props.setTitle(`${this.slotId} `);
        this.fetchSlotInfo();
    }

    fetchSlotInfo() {
        fetchDetail(this.slotId).then(res => {
            this.setState({slot: res});
            this.props.setTitle(`${res.slotNo} `);
            ActionButtons[3] = res.indivisible ? '' : '';
        })
    }

    render() {
        const {slot} = this.state;
        const sensors = slot.sensors || [];
        return (
            <div className="slotDetail">
                <List renderHeader={() => ''}>
                    <InputItem placeholder="" value={slot.slotNo}
                               onChange={text => this.setUpdateSlotProp({slotNo: text})}></InputItem>
                    <InputItem placeholder="SKU" value={slot.skuName}
                               onChange={text => this.setUpdateSlotProp({skuName: text})}>SKU</InputItem>
                    <InputItem placeholder="SKU" value={slot.skuNo}
                               onChange={text => this.setUpdateSlotProp({skuNo: text})}>SKU</InputItem>
                    <InputItem placeholder="SKU" value={slot.skuApw}
                               type="money"
                               onChange={text => this.setUpdateSlotProp({skuApw: text})}>SKU</InputItem>
                    <InputItem placeholder="SKU" value={slot.skuTolerance}
                               type="money"
                               onChange={text => this.setUpdateSlotProp({skuTolerance: text})}>SKU</InputItem>
                    <InputItem placeholder="" value={slot.skuShelfLifeOpenDays}
                               type="number"
                               onChange={text => this.setUpdateSlotProp({skuShelfLifeOpenDays: text})}>
                        
                    </InputItem>
                    <Item key="applyBtn">
                        <WingBlank><Button type="primary" onClick={() => this.applyModify()}></Button></WingBlank>
                    </Item>
                </List>
                <List renderHeader={() => 'Operations'}>
                    <List.Item
                        key="hasELabel"
                        extra={<Switch
                            checked={slot.hasElabel}
                            onChange={state => this.toggleELabel(state)}
                            platform="android"/>}>
                        
                    </List.Item>
                    <List.Item extra={<Switch
                        checked={slot.indivisible}
                        platform="android"
                        onChange={state => this.operateIndivisible(state)}/>}>
                        /
                    </List.Item>
                </List>
                <List renderHeader={() => ''}>
                    {
                        sensors.map(sensor => (<List.Item key={sensor.id} extra={sensor.deviceSn}>
                            {sensor.address}
                        </List.Item>))
                    }
                </List>
                <FloatButton iconType="ellipsis" onClick={() => this.openOperation()}/>
            </div>
        );
    }

    operateIndivisible(state) {
        const title = state ? '' : '';
        const message = state ? '' : '';
        const _this = this;
        if (state) {
            Modal.alert(title, message, [{text: ''}, {
                text: '',
                onPress() {
                    _this.doOperateIndivisible(state, null);
                }
            }]);
        } else {
            Modal.prompt(title, message,
                [
                    {
                        text: ''
                    },
                    {
                        text: '',
                        onPress(password) {
                            _this.doOperateIndivisible(state, password);
                        }
                    }], 'secure-text', null, ['']);
        }
    }

    doOperateIndivisible(state, password) {
        const {id} = this.state.slot;
        const options = {
            indivisible: state,
            password,
            id,
        };
        const _this = this;
        lockSlot(options).then(res => {
            Toast.show(`${res}!`, 3, false);
            _this.fetchSlotInfo();
        });
    }

    openOperation() {
        ActionSheet.showActionSheetWithOptions({
            title: '',
            options: ActionButtons,
            destructiveButtonIndex: 1,
            cancelButtonIndex: ActionButtons.length - 1,
        }, buttonIndex => {
            switch (buttonIndex) {
                case 0:
                    this.doZero();
                    break;
                case 1:
                    Toast.show('', 2, false);
                    break;
                case 2:
                    this.toggleEnableState();
                    break;
                case 3:
                    this.showLockModal();
                    break;
                default:
                //Do nothing
            }
        });
    }

    showLockModal() {
        const {slot} = this.state;
        const title = slot.indivisible ? '' : '';
        const message = slot.indivisible ? '\r\n' : '\r\n';
        const indivisible = slot.indivisible ? 'false' : 'true';
        const states = slot.indivisible ? '' : '';
        const _this = this;
        Modal.prompt(title, message,
            [
                {
                    text: ''
                },
                {
                    text: '',
                    onPress(password) {
                        const options = {
                            id: _this.slotId,
                            password,
                            indivisible,
                        };
                        lockSlot(options).then(res => {
                            Toast.show(`${states}${res}`, 3, false);
                            _this.fetchSlotInfo();
                        });
                    }
                }], 'secure-text', null, [`${states}`]);
    }

    toggleEnableState() {
        toggleEnableState(this.slotId).then(res => {
            Toast.show(`${res.slotNo}`, 3, false);
        });
    }

    doZero() {
        const {slot} = this.state;
        zeroOne(slot.slotNo).then(() => {
            Toast.show('!');
        });
    }

    setUpdateSlotProp(props) {
        const slot = {
            ...this.state.slot,
            ...props
        };
        this.setState({slot});
    }

    applyModify() {
        Modal.alert("", "", [
            {text: ''},
            {
                text: '',
                onPress() {
                    if (!CAN_UPDATE_SLOT_INFO) {
                        Modal.alert("", "", [{text: ''}]);
                        return;
                    }
                    updateSlot(this.state.slot).then(() => {
                        Toast.show('Apply Success!');
                    });
                }
            },
        ]);
    }

    toggleELabel(hasELabel) {
        toggleELabelState({id: this.slotId, hasELabel}).then(() => {
            Toast.show('Toggle ELabel Success!');
            this.fetchSlotInfo();
        });
    }

}

export default withRouter(connect(null, mapAction2Props)(SlotDetailSetting));
